/* Typography */

body {
  font-size: 1rem;
  font-family: $type1-regular;
  font-weight: initial;
}
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: $type1-bold;
}
p {
  font-size: $default-font-size;
}
h1,
.h1 {
  font-size: 2.19rem;
}
h2,
.h2 {
  font-size: 1.88rem;
}
h3,
.h3 {
  font-size: 1.56rem;
}
h4,
.h4 {
  font-size: 1.13rem;
}
h5,
.h5 {
  font-size: 1rem;
}
h6,
.h6 {
  font-size: .9375rem;
}
p {
  font-size: .88rem;
}
.display-1 {
  font-size: 3.75rem;
  @media (max-width: 991px) {
    font-size: 3rem;
  }
}
.display-2 {
  font-size: 3.125rem;
  @media (max-width: 991px) {
    font-size: 2.5rem;
  }
}
.display-3 {
  font-size: 2.5rem;
  @media (max-width: 991px) {
    font-size: 2rem;
  }
}
.display-4 {
  font-size: 1.875rem;
  @media (max-width: 991px) {
    font-size: 1.5rem;
  }
}
.display-5 {
  font-size: 1.25rem;
  @media (max-width: 991px) {
    font-size: 1rem;
  }
}
.blockquote {
  padding: 1.25rem;
  border: 1px solid $border-color;
}

address {
  p {
    margin-bottom: 0;
  }
}

//blockqoute color variations
@each $color, $value in $theme-colors {
  .blockquote-#{$color} {
    @include blockquote($value);
  }
}
.error-page {
  h1 {
    font-size: 12rem;
    @media (max-width: 991px) {
      font-size: 8rem;
    }
  }
}
.page-title {
  color: $black;
  margin: 0.38rem 0 0.75rem 0;
}
.card-title {
  color: color(black);
  margin-bottom: 1.125rem;
  text-transform: capitalize;
}
.card-subtitle {
  @extend .text-gray;
  font-family: $type1-regular;
  margin-top: 0.625rem;
  margin-bottom: 0.625rem;

}
.card-description{
  margin-bottom: .9375rem;
  font-family: $type1-regular;
}
.font-weight-normal {
  font-family: $type1-regular !important;
}
.bold-text {
  font-family: $type1-bold;
}
.text-small{
  font-size: $default-font-size;
}
.icon-lg {
  font-size: 3.438rem;
}
.icon-md {
  font-size: 1.875rem;
}
.icon-sm {
  font-size: 1rem;
}
